<template>
     <el-card shadow='hover'>
        <div class="basic-box">
            <div class="avatar-box" @click="openUploadDialog">
                <avatar :name='user_abbr' :headIcon ='user_headicon' :bgColor='user_backcolor'  />
                <div class="change">更换</div>
            </div>
            <div class="item">用户名：{{account}}</div>
            <div class="item">加入时间：<span>{{ptime}}</span></div>
            <div class="item">邮箱：<span>Htest001@beehub.com</span></div>
            <div class="item">头像背景色：
                 <span class="user_bg" :style="{background:item}" @click="addUserBgColor(index)"
                 v-for="(item, index) in user_bg_list" :key="index">
                    <i class="el-icon-check" v-if="user_backcolor==user_bg_list[index]" style="color:#fff;position:relative;left:8px;top:8px"></i>
                 </span>
            </div>
            <div class="item">
                昵称：
                <el-input size="small" placeholder="昵称不能为空且大于3位"  @keyup.enter.native="changenick"
                  :value="user_name" v-model="user_name" class="nickInput"></el-input>
            </div>
            <div class="item">
                <el-button type="primary" :loading="loading"
                @click.native.prevent="changenick">确定</el-button>
            </div>
        </div>

        <el-dialog title="图片上传" :visible.sync="dialogTableVisible">
            <div style="margin-bottom:20px">
                图片上传
                 <label  for="uploads">
                    <el-button size="small" type="primary" @click="uploadFileBtn">
                        选择图片
                    </el-button>
                </label>

                 <input type="file" ref='uploads' id="uploads" style="position:absolute; clip:rect(0 0 0 0);"
                accept="image/png, image/jpeg, image/gif, image/jpg" @change="uploadImg($event, 1)"/>
            </div>
            <el-row :gutter="20">
                <el-col :span="20">
                    <div  style="height:400px">
                        <vue-cropper
                        ref="cropper"
                        :img="option.img"
                        :outputSize="option.size"
                        :outputType="option.outputType"
                        :info="true"
                        :full="option.full"
                        :canMove="option.canMove"
                        :canMoveBox="option.canMoveBox"
                        :fixedBox="option.fixedBox"
                        :original="option.original"
                        :autoCrop="option.autoCrop"
                        :autoCropWidth="option.autoCropWidth"
                        :autoCropHeight="option.autoCropHeight"
                        :centerBox="option.centerBox"
                        :high="option.high"
                        :infoTrue="option.infoTrue"
                        :maxImgSize="option.maxImageSize"
                        @realTime= 'realTime'
                        @imgLoad="imgLoad"
                        @cropMoving="cropMoving"
                        :enlarge="option.enlarge"
                        :mode="option.mode"
                        ></vue-cropper>
                        </div>
                </el-col>
                <el-col :span="4">
                    <div class="show-preview" :style="{'width': previews.w + 'px', 'height': previews.h + 'px',  'overflow': 'hidden',
							'margin': '5px'}">
							<div :style="previews.div">
								<img :src="previews.url" :style="previews.img">
							</div>
						</div>
                </el-col>
            </el-row>
            <div class="btn-group-cropper">
                  <el-row :gutter="20">
                    <el-col :span="20">
                        <el-row :gutter="20">
                            <el-col :span="12">
                                <el-button size="small" icon="el-icon-refresh-left"  @click="rotateLeft">向左旋转</el-button>
                                <el-button size="small" icon="el-icon-refresh-right"  @click="rotateRight">向右旋转</el-button>
                            </el-col>
                            <el-col :span="12" style="text-align:right">
                                 <el-button size="small" icon="el-icon-refresh"  @click="refreshCrop">刷新</el-button>
                                 <el-button size="small" icon="el-icon-zoom-in"  @click="changeScale(1)">放大</el-button>
                                 <el-button size="small" icon="el-icon-zoom-out"  @click="changeScale(-1)">缩小</el-button>
                            </el-col>
                        </el-row>
                    </el-col>
                     <el-col :span="4" style="text-align:center">
                         <el-button size="small"  type="primary" :loading="loading" @click="saveHeadIcon">保存头像</el-button>
                    </el-col>
                  </el-row>
            </div>
        </el-dialog>
    </el-card>
</template>
<script>
import Avatar from '@/components/Avatar'
import { mapState } from '@icony/vue-container/vuex'
import { VueCropper }  from 'vue-cropper'
import test  from '@/assets/images/networkdoesnotforce.png'
export default {
    name: 'BasicDoc',
    components: {
        Avatar,
        VueCropper
    },
    data() {
        return {
            loading:false,
            dialogTableVisible:false,
            user_bg_list: ['#FF7765','#FFB800','#2FC9AE','#169BD5','#556D84','#8400FF'],
            previews: {},
            option: {
                img: "",
                size: 1,
                full: false,
                outputType: "png",
                canMove: true,
                fixedBox: true,
                original: false,
                canMoveBox: true,
                autoCrop: true,
                // 只有自动截图开启 宽度高度才生效
                autoCropWidth: 120,
                autoCropHeight: 120,
                fixed:true,
                fixedNumber:[1,1],
                centerBox: false,
                high: false,
                cropData: {},
                enlarge: 1,
                mode: 'contain',
                maxImgSize: 2000
            }
        }
    },
    computed: {
        ...mapState({
            user_abbr: state => state.user.user.user_abbr,
            user_name: state => state.user.user.user_name,
            user_backcolor: state => state.user.user.user_backcolor,
            user_headicon:state => state.user.user.user_headicon,
            account: state => state.user.user.account,
            ptime: state => state.user.user.ptime
        })
    },
    methods: {
        addUserBgColor(index){
            // 获取截图的base64 数据
            // this.$store.dispatch("user/changeheadicon", {user_headicon: ''}).then(()=>{
                this.$store.dispatch('user/changenick', {user_name: this.user_name, user_backcolor: this.user_bg_list[index]}).then(()=>{
                    this.loading = false
                    this.$message({
                        message: '更新成功！',
                        type: 'success'
                    });
                    this.$store.dispatch('user/getuser')
                }).catch(()=>{
                    this.loading = false
                })
            // }).catch(()=>{
            //     this.loading = false
            // })
        },
        openUploadDialog(){
            this.dialogTableVisible = true
        },
        uploadFileBtn(){
            this.$refs.uploads.click()
        },
        refreshCrop() {
            // clear
            this.$refs.cropper.refresh();
        },
        rotateLeft() {
            this.$refs.cropper.rotateLeft();
        },
        rotateRight() {
            this.$refs.cropper.rotateRight();
        },
        changeScale(num) {
            num = num || 1;
            this.$refs.cropper.changeScale(num);
        },
        uploadImg(e, num) {
            //上传图片
            // this.option.img
            var file = e.target.files[0];
            if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
                this.$message({
                    message: '图片类型必须是.gif,jpeg,jpg,png,bmp中的一种',
                    type: 'warn'
                });
                return false;
            }
            var reader = new FileReader();
            reader.onload = e => {
                let data;
                if (typeof e.target.result === "object") {
                // 把Array Buffer转化为blob 如果是base64不需要
                data = window.URL.createObjectURL(new Blob([e.target.result]));
                } else {
                data = e.target.result;
                }
                if (num === 1) {
                this.option.img = data;
                }
            };
            // 转化为base64
            // reader.readAsDataURL(file)
            // 转化为blob
            reader.readAsArrayBuffer(file);
        },
        // 实时预览函数
        realTime(data) {
            this.previews = data;
        },
        imgLoad(msg) {
            console.log(msg);
        },
        cropMoving(data) {
            this.option.cropData = data;
        },
        saveHeadIcon(){
            // 获取截图的base64 数据
            this.$refs.cropper.getCropData((data) => {
                console.log('xxxxxxxxxxxxxxxxxxxxxxxxx data ', data, typeof data)
                this.loading = true
                this.$store.dispatch("user/changeheadicon",{user_headicon:data}).then(()=>{
                    this.loading = false
                    this.dialogTableVisible = false
                    this.$message({
                        message: '更新成功！',
                        type: 'success'
                    });
                }).catch(()=>{
                    this.loading = false
                })
            })
        },
        changenick(){
            if(this.user_name && this.user_name.length>3){
                var regexp = /^[\u4e00-\u9fa5_a-zA-Z]*$/
                const flag = regexp.test(this.user_name)
                if(flag){
                    this.loading = true
                    let data = {}
                    this.$store.dispatch('user/changenick',{user_name:this.user_name,user_backcolor:this.user_backcolor}).then(()=>{
                        this.loading = false
                        this.$message({
                            message: '更新成功！',
                            type: 'success'
                        });
                    }).catch(()=>{
                        this.loading = false
                    })
                }else{
                    this.$message({
                        message: '昵称只支持中文，英文字母，下划线',
                        type: 'error'
                    });
                }

            }else{
                this.$message({
                    message: '昵称不能为空且大于3位',
                    type: 'error'
                });
            }

        }
    },
}
</script>
<style lang="scss" scoped>
.avatar-box {
    position: absolute;
    top:-10px;
    left:-105px;
    overflow: hidden;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    cursor: pointer;
}

.upload {
    display: inline-block;
    margin-left: 20px;
}

.change {
    position: absolute;
    top: 40px;
    font-size: 12px;
    width:100%;
    background: rgba(0,0,0,0.1);
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #ffffff;
}
.basic-box {
    margin-left: 150px;
    position: relative;
}
.item {
     margin: 30px;
 }

 .btn-group-cropper {
     margin-top: 20px;
 }

.user_bg {
    display: inline-block;
    width: 34px;
    height: 34px;
    margin-right: 20px;
    vertical-align: middle;
    cursor: pointer;
}
 .nickInput {
     display: inline-block;
     width:200px;
 }
</style>
